import {
  initAwwardsBadge,
  initHomeAnimations,
  initHoverAnimations,
  initLoad,
  initNav,
  initProcessScroll,
  initServiceHover,
  initTestiSwiper,
  initTextUnderlineScroll,
  initWorkAnimations,
} from './animations/orris';
import { initAos, initAosScale, initStaggeredAos } from './plugins/aos';
import { initLenis } from './plugins/lenis';
import { getLenis } from './plugins/lenis';
import { initTrMarquee } from './plugins/scroll-marquee';
import {
  initFooterOverlap,
  initSectionOverlapFull,
  initSectionOverlapNext,
  initSectionOverlapPrev,
} from './plugins/section-overlap';
import { showHideGrid } from './plugins/showHideGrid';
import { splitLines } from './plugins/split-text';

CustomEase.create('primary-ease', '0.62, 0.05, 0.01, 0.99');
CustomEase.create('primary-ease-out', '0.17,0.84,0.44,1');
CustomEase.create('smooth', '.15, .9, .34, .95');
CustomEase.create('custom', '.76, 0, .24, 1');

ScrollTrigger.defaults({
  markers: false,
});

function initApp() {
  const mm = gsap.matchMedia();

  initLenis();
  window.lenis = getLenis();
  showHideGrid();
  //reusable functions
  splitLines();
  initAos();
  initAosScale();
  initStaggeredAos();

  mm.add('(min-width: 480px)', () => {
    initHomeAnimations();
    initServiceHover();
  });

  initWorkAnimations();
  initTrMarquee();
  initAwwardsBadge();
  initProcessScroll();
  initTextUnderlineScroll();
  initTestiSwiper();
  initHoverAnimations();
  initNav();

  initLoad();

  mm.add('(min-width: 992px)', () => {
    initSectionOverlapPrev();
    initSectionOverlapNext();
    initSectionOverlapFull();
    initFooterOverlap();
  });
}

document.addEventListener('DOMContentLoaded', () => {
  initApp();
});
