import { getLenis } from 'src/plugins/lenis';
import { createSplitLines } from 'src/plugins/split-text';

export function initWorkAnimations() {
  const wrapper = document.querySelector('.work-wrapper');
  if (!wrapper) return;
  const inner = wrapper.querySelector('.work-inner');
  const featuredGrup = wrapper.querySelector('.work-featured-group');
  const row = wrapper.querySelector('.work-row');
  const featured = wrapper.querySelector('.work-featured');
  const column = wrapper.querySelector('.work-column');

  const display = wrapper.querySelector('.work-display');
  const items = wrapper.querySelectorAll('.work-img-wrap');

  const splitDisplay = new SplitText(display, {
    type: 'chars',
    mask: 'chars',
  });

  const mm = gsap.matchMedia();
  const mobile = window.matchMedia('(max-width: 479px)').matches;

  if (mobile) {
    gsap.set(splitDisplay.chars, { yPercent: 100, autoAlpha: 0 });

    items.forEach((item, index) => {
      const overlay = item.querySelector('.work-item-overlay');
      const itemText = item.querySelector('.work-item-text');

      const split = new SplitText(itemText, {
        type: 'chars',
        mask: 'chars',
      });

      gsap.set(overlay, { autoAlpha: 0 });
      gsap.set(split.chars, { yPercent: 100 });

      const tlItem = gsap.timeline({ paused: true });

      tlItem.to(overlay, { autoAlpha: 1, duration: 0.3, ease: 'custom' }, 0);
      tlItem.to(
        split.chars,
        {
          yPercent: 0,
          duration: 0.3,
          ease: 'custom',
          stagger: {
            amount: 0.2,
            from: 'start',
          },
        },
        0
      );

      item.addEventListener('mouseenter', () => {
        gsap.to(items, {
          filter: 'grayscale(100%)',
          duration: 0.3,
          ease: 'custom',
        });

        // tapi item yang dihover grayscale 0
        gsap.to(item, {
          filter: 'grayscale(0%)',
          duration: 0.3,
          ease: 'custom',
        });

        tlItem.play();
      });

      item.addEventListener('mouseleave', () => {
        // jika mouse keluar dari item, cek apakah masih ada item lain yang dihover
        const isAnyHovered = Array.from(items).some((i) => i.matches(':hover'));

        if (!isAnyHovered) {
          // jika tidak ada item yang dihover, reset semua ke grayscale 0
          gsap.to(items, {
            filter: 'grayscale(0%)',
            duration: 0.3,
            ease: 'power2.out',
          });
        }

        tlItem.reverse();
      });
    });
  } else {
    gsap.set(featuredGrup, { width: '100vw' });
    gsap.set(featured, { height: '100vh' });
    gsap.set(row, { height: '35vh' });
    gsap.set(column, { width: '35vw' });
    gsap.set(splitDisplay.chars, { yPercent: 100, autoAlpha: 0 });

    ScrollTrigger.create({
      trigger: wrapper,
      start: 'top top',
      end: 'bottom bottom',
      pin: inner,
      pinSpacing: false,
    });

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: wrapper,
        start: 'top top',
        end: '90% bottom',
        scrub: true,
        onUpdate: (self) => {
          // self.progress = nilai dari 0 ke 1
          const { progress } = self;

          // jika belum sampai bagian akhir (misal < 0.90)
          if (progress < 0.9) {
            gsap.set(items, { pointerEvents: 'none' });
          } else {
            gsap.set(items, { pointerEvents: 'auto' });
          }
        },
        onComplete: () => {
          gsap.set(items, { pointerEvents: 'auto' });
        },
      },
    });

    tl.to(featured, { height: '65vh', ease: 'custom' });
    tl.to(splitDisplay.chars, { yPercent: 0, autoAlpha: 1, ease: 'custom' }, 0);
    tl.to(featuredGrup, { width: '65vw', ease: 'custom' });
    tl.to(splitDisplay.chars, { autoAlpha: 0, filter: 'blur(10px)', ease: 'custom' }, '<');

    items.forEach((item, index) => {
      const overlay = item.querySelector('.work-item-overlay');
      const itemText = item.querySelector('.work-item-text');

      const split = new SplitText(itemText, {
        type: 'chars',
        mask: 'chars',
      });

      gsap.set(overlay, { autoAlpha: 0 });
      gsap.set(split.chars, { yPercent: 100 });

      const tlItem = gsap.timeline({ paused: true });

      tlItem.to(overlay, { autoAlpha: 1, duration: 0.3, ease: 'custom' }, 0);
      tlItem.to(
        split.chars,
        {
          yPercent: 0,
          duration: 0.3,
          ease: 'custom',
          stagger: {
            amount: 0.2,
            from: 'start',
          },
        },
        0
      );

      item.addEventListener('mouseenter', () => {
        gsap.to(items, {
          filter: 'grayscale(100%)',
          duration: 0.3,
          ease: 'custom',
        });

        // tapi item yang dihover grayscale 0
        gsap.to(item, {
          filter: 'grayscale(0%)',
          duration: 0.3,
          ease: 'custom',
        });

        tlItem.play();
      });

      item.addEventListener('mouseleave', () => {
        // jika mouse keluar dari item, cek apakah masih ada item lain yang dihover
        const isAnyHovered = Array.from(items).some((i) => i.matches(':hover'));

        if (!isAnyHovered) {
          // jika tidak ada item yang dihover, reset semua ke grayscale 0
          gsap.to(items, {
            filter: 'grayscale(0%)',
            duration: 0.3,
            ease: 'power2.out',
          });
        }

        tlItem.reverse();
      });
    });
  }
}

export function initServiceHover() {
  const serviceItems = document.querySelectorAll('[data-service].service-item');
  const serviceImgWrap = document.querySelector('.service-img-wrap');
  const serviceDisplayText = document.querySelector('.service-display-text');
  const serviceImgInners = document.querySelectorAll('.service-img-inner');
  const serviceTextWraps = document.querySelectorAll('.service-text-wrap');
  const serviceDisplay = document.querySelector('.service-display');

  let activeIndex = 0;

  if (serviceItems.length > 0) {
    serviceItems[0].classList.add('is-active');
  }

  let currentTimeline = null;
  let activeServiceType = null;

  const activeTl = null; // simpan timeline yang sedang aktif
  const activeItem = null; // simpan item aktif sekarang

  // Set initial state
  gsap.set(serviceImgInners, {
    yPercent: 100,
    pointerEvents: 'none',
    zIndex: 1,
  });

  gsap.set(serviceTextWraps, {
    opacity: 0,
    pointerEvents: 'none',
  });

  function animateService(index, serviceType) {
    if (serviceType === activeServiceType) return; // skip jika service sama
    activeServiceType = serviceType;

    if (currentTimeline) currentTimeline.kill();

    const imgInner = document.querySelectorAll(`.service-img-inner`);
    const activeImg = document.querySelector(`.service-img-inner[data-service="${serviceType}"]`);
    const activeText = document.querySelector(`.service-text-wrap[data-service="${serviceType}"]`);

    // Reset semua z-index
    imgInner.forEach((img) => gsap.set(img, { zIndex: 1 }));

    // Naikkan elemen aktif ke paling atas
    gsap.set(activeImg, { zIndex: 8 });

    currentTimeline = gsap.timeline({
      defaults: { ease: 'power2.inOut' },
      onComplete: () => {
        // Setelah animasi selesai, pastikan hanya elemen aktif yang tetap di atas
        imgInner.forEach((img) => {
          gsap.set(img, { zIndex: img === activeImg ? 8 : 1 });
        });
      },
    });

    // 1️⃣ Hide semua teks
    currentTimeline.to(
      serviceTextWraps,
      {
        opacity: 0,
        duration: 0.3,
        onComplete: () => serviceTextWraps.forEach((t) => (t.style.pointerEvents = 'none')),
      },
      0
    );

    // 2️⃣ Scale-out semua gambar
    currentTimeline.to(
      imgInner,
      {
        yPercent: 100,
        scale: 0.8,
        duration: 0.5,
        ease: 'custom',
      },
      0
    );

    // 3️⃣ Scale-in gambar aktif
    currentTimeline.to(
      activeImg,
      {
        yPercent: 0,
        scale: 1,
        duration: 0.8,
        ease: 'custom',
        onStart: () => (activeImg.style.pointerEvents = 'auto'),
      },
      0.05
    );

    // 4️⃣ Fade-in teks aktif
    currentTimeline.to(
      activeText,
      {
        opacity: 1,
        duration: 0.5,
        onStart: () => (activeText.style.pointerEvents = 'auto'),
      },
      0.05
    );
  }

  function setActiveItem(newIndex) {
    if (newIndex === activeIndex) return;

    // Hilangkan active sebelumnya
    serviceItems[activeIndex].classList.remove('is-active');
    // Tambahkan active baru
    serviceItems[newIndex].classList.add('is-active');

    // Geser display (opsional, seperti sebelumnya)
    const totalItems = serviceItems.length - 1;
    const percentage = totalItems > 0 ? newIndex / totalItems : 0;
    const offset = percentage * 80;

    gsap.to(serviceDisplay, {
      yPercent: offset,
      duration: 0.6,
      ease: 'power2.out',
      overwrite: 'auto',
    });

    activeIndex = newIndex;
  }

  // Hover listener
  serviceItems.forEach((item, index) => {
    const inner = item.querySelector('.service-item-inner');
    const icon = item.querySelector('.service-icon');

    item.addEventListener('mouseenter', () => {
      const serviceType = item.getAttribute('data-service');

      animateService(index, serviceType);
      setActiveItem(index);
    });
  });

  // Inisialisasi pertama
  if (serviceItems.length > 0) {
    const firstItem = serviceItems[0];
    const firstType = firstItem.getAttribute('data-service');
    const firstInner = firstItem.querySelector('.service-item-inner');
    const firstIcon = firstItem.querySelector('.service-icon');

    // animasi gambar & teks pertama
    animateService(0, firstType);
  }
}

export function initAwwardsBadge() {
  const wrapper = document.querySelector('.award-wrapper');
  if (!wrapper) return;

  const head = wrapper.querySelector('.award-head');
  const headLeft = wrapper.querySelector('.award-heading-wrap.is-left');
  const headRight = wrapper.querySelector('.award-heading-wrap.is-right');

  gsap.fromTo(
    headLeft,
    { x: '-30vw' },
    {
      x: '0vw',
      duration: 1,
      ease: 'none',
      scrollTrigger: {
        trigger: head,
        start: 'top bottom',
        end: 'bottom top',
        scrub: true,
      },
    }
  );

  gsap.fromTo(
    headRight,
    { x: '30vw' },
    {
      x: '0vw',
      duration: 1,
      ease: 'none',
      scrollTrigger: {
        trigger: head,
        start: 'top bottom',
        end: 'bottom top',
        scrub: true,
      },
    }
  );

  const awardGroups = wrapper.querySelectorAll('.award-item');

  awardGroups.forEach((group) => {
    const projectLinks = group.querySelectorAll('.award-project-link');
    const previewImages = group.querySelectorAll('.award-image');
    const projectName = group.querySelector('.award-name');
    const awardTitles = group.querySelectorAll('.award-tittle');
    let currentActive = null;

    function activateProject(projectId, awards = []) {
      // Reset semua state dalam group ini
      projectLinks.forEach((link) => link.classList.remove('is-active'));
      previewImages.forEach((img) => img.classList.remove('is-active'));
      awardTitles.forEach((title) => title.classList.remove('is-active'));

      // Aktifkan project link dan image berdasarkan ID
      if (projectId) {
        projectLinks.forEach((link) => {
          if (link.dataset.project === projectId) link.classList.add('is-active');
        });
        previewImages.forEach((img) => {
          if (img.dataset.project === projectId) img.classList.add('is-active');
        });

        // Aktifkan award title berdasarkan data-award yang cocok
        if (awards.length) {
          awardTitles.forEach((title) => {
            if (awards.includes(title.dataset.award)) {
              title.classList.add('is-active');
            }
          });
        }
      }

      currentActive = projectId;
    }

    // Hover pada project link di dalam group
    projectLinks.forEach((link) => {
      link.addEventListener('mouseenter', (e) => {
        const projectId = e.target.dataset.project;
        const awardData = e.target.dataset.award || '';
        const awards = awardData
          .split(/[, ]+/)
          .map((a) => a.trim())
          .filter(Boolean); // dukung multi-award

        activateProject(projectId, awards);
      });

      link.addEventListener('click', (e) => e.preventDefault());
    });

    // Hover pada group → aktifkan award name
    group.addEventListener('mouseenter', () => {
      if (projectName) projectName.classList.add('is-active');

      // Tetap aktifkan project default (pertama)
      const firstLink = group.querySelector('.award-project-link');
      if (firstLink) {
        const projectId = firstLink.dataset.project;
        const awardData = firstLink.dataset.award || '';
        const awards = awardData
          .split(/[, ]+/)
          .map((a) => a.trim())
          .filter(Boolean);
        activateProject(projectId, awards);
      }
    });

    // Keluar dari group → reset semua state
    group.addEventListener('mouseleave', () => {
      if (projectName) projectName.classList.remove('is-active');
      activateProject(null);
    });
  });
}

export function initProcessScroll() {
  const processWrapper = document.querySelector('.proces-wrapper');
  if (!processWrapper) return;

  const processItems = processWrapper.querySelectorAll('.proces-item');

  processItems.forEach((item) => {
    gsap.from(item, {
      x: '10vw',
      opacity: 0,
      ease: 'power2.out',
      scrollTrigger: {
        trigger: item,
        start: 'top 90%',
        end: 'top center',
        scrub: true,
      },
    });
  });
}

export function initTextUnderlineScroll() {
  const texts = document.querySelectorAll('.text-underline');
  if (!texts.length) return;

  texts.forEach((text) => {
    gsap.to(text, {
      '--scale': 1,
      duration: 0.6,
      ease: 'power3.out',
      scrollTrigger: {
        trigger: text,
        start: 'top 90%',
        toggleActions: 'play none none reverse',
      },
    });
  });
}

export function initTestiSwiper() {
  const testiWrap = document.querySelector('.swiper.testi-wrapper');
  if (!testiWrap) return;

  const nextBtn = testiWrap.querySelector('.swiper-nav.is-next');
  const prevBtn = testiWrap.querySelector('.swiper-nav.is-prev');

  const swiper = new Swiper(testiWrap, {
    loop: true,
    slidesPerView: 1,
    spaceBetween: 20,
    speed: 800,
    navigation: {
      nextEl: nextBtn,
      prevEl: prevBtn,
    },
  });
}

export function initLoad() {
  const navLinks = document.querySelectorAll('.nav-link');
  const navBrand = document.querySelector('.nav-brand-wrap');
  const head = document.querySelector('.hero-head');
  const text = document.querySelectorAll('.hero-text'); // ambil semua jika lebih dari 1
  const navToggle = document.querySelector('.nav-toggle');
  const brandItem = document.querySelector('.nav-brand');
  const navPath = document.querySelectorAll('.nav-path');

  const headSplit = createSplitLines(head, { stagger: 0.03 });
  const titleSplit = createSplitLines(text, { stagger: 0.03 });

  const preload = document.querySelector('.preload');

  const mm = gsap.matchMedia();

  const mobile = window.matchMedia('(max-width: 479px)').matches;

  const preloadTl = gsap.timeline({
    onComplete: () => {
      preload.style.display = 'none';
      loadTl.play();
      lenis.start();
      lenis.resize();
      ScrollTrigger.refresh();
    },
  });

  setTimeout(() => {
    lenis.start();
    lenis.resize();
    ScrollTrigger.refresh();
  }, 4000);

  setTimeout(() => {
    lenis.resize();
    ScrollTrigger.refresh();
  }, 5000);

  const loadTl = gsap.timeline({ paused: true });

  // Set initial state
  gsap.set(preload, { y: '100%' });
  gsap.set(navLinks, { y: '1rem', autoAlpha: 0 });
  gsap.set(navToggle, { y: '1rem', autoAlpha: 0 });
  gsap.set(navPath, { y: '110%' });

  if (mobile) {
    gsap.set(navBrand, { height: 'calc(100dvh - 2em)' });
    gsap.set(brandItem, { width: '15.75em' });
    // preload animation
    preloadTl.to(
      navPath,
      {
        y: '0%',
        duration: 1.2,
        stagger: {
          each: 0.8,
          from: 'end',
        },
        ease: 'primary-ease',
      },
      0
    );
    preloadTl.to(navBrand, {
      height: 'auto',
      duration: 1.2,
      ease: 'custom',
      delay: 0.2,
    });
    preloadTl.to(preload, { y: '0%', duration: 1.2, ease: 'primary-ease' });
  } else {
    gsap.set(navBrand, { height: 'calc(100dvh - 2em)' });
    // preload animation
    preloadTl.to(
      navPath,
      {
        y: '0%',
        duration: 1.2,
        stagger: {
          each: 0.75,
          from: 'end',
        },
        ease: 'custom',
      },
      0
    );
    preloadTl.to(navBrand, {
      height: 'auto',
      duration: 1.2,
      ease: 'custom',
    });
    preloadTl.to(preload, { y: '0%', duration: 1.2, ease: 'primary-ease' }, '-=0.3');
  }

  if (mobile) {
    loadTl.to(
      brandItem,
      {
        width: '3.75em',
        duration: 0.8,
        ease: 'power2.out',
      },
      0
    );
  }
  loadTl
    .to(
      navLinks,
      {
        y: '0rem',
        autoAlpha: 1,
        duration: 0.8,
        ease: 'power2.out',
      },
      0
    )
    .to(
      navToggle,
      {
        y: '0rem',
        autoAlpha: 1,
        duration: 0.8,
        ease: 'power2.out',
      },
      0
    )
    .to(preload, { y: '-100%', duration: 1.2, ease: 'primary-ease' }, '-=0.5')
    .add(titleSplit.timeline, 0)
    .add(headSplit.timeline, 0);

  window.addEventListener('load', () => {
    lenis.scrollTo(0, { duration: 0.1, immediate: true });
    history.scrollRestoration = 'manual';
    setTimeout(() => {
      lenis.stop();
      setTimeout(() => {
        preloadTl.play();
      }, 100);
    }, 100);
  });
}

export function initHomeAnimations() {
  const navBrand = document.querySelector('.nav-brand');
  const hero = document.querySelector('.hero.is-home');
  if (!navBrand || !hero) return;

  gsap.from(navBrand, {
    width: '100%',
    duration: 1,
    ease: 'none',
    scrollTrigger: {
      trigger: hero,
      start: 'top top',
      end: '80% top',
      scrub: true,
    },
  });
}

export function initHoverAnimations() {
  function initCtaHover() {
    const btn = document.querySelector('.cta-btn');
    if (!btn) return;

    const text = btn.querySelector('.btn-text');
    const absoluteText = btn.querySelector('.btn-text.is-absolute');
    const underline = btn.querySelector('.cta-underline');

    const tl = gsap.timeline({ paused: true });

    const splitText = new SplitText(text, {
      type: 'chars',
    });

    const splitAbsoluteText = new SplitText(absoluteText, {
      type: 'chars',
    });

    tl.to(
      splitText.chars,
      {
        yPercent: -100,
        duration: 0.6,
        ease: 'custom',
        stagger: {
          amount: 0.1,
          from: 'start',
        },
      },
      0
    );

    tl.to(
      splitAbsoluteText.chars,
      {
        yPercent: -100,
        duration: 0.4,
        ease: 'custom',
        stagger: {
          amount: 0.1,
          from: 'start',
        },
      },
      0.2
    );

    tl.to(
      underline,
      {
        width: '20.5625em',
        duration: 0.6,
        ease: 'custom',
      },
      0
    );

    btn.addEventListener('mouseenter', () => {
      tl.play();
    });

    btn.addEventListener('mouseleave', () => {
      tl.reverse();
    });
  }

  initCtaHover();
}

export function initNav() {
  const nav = document.querySelector('.navbar');
  if (!nav) return;
  const navMobile = document.querySelector('.nav-mobile');
  if (!navMobile) return;
  const navLinks = navMobile.querySelectorAll('.nav-mobile-link');
  const btnWrap = navMobile.querySelector('.nav-btn-wrap');
  const toggle = nav.querySelector('.nav-toggle');

  const text = toggle.querySelector('.text-16');
  const absoluteText = toggle.querySelector('.text-16.is-absolute');

  let isOpen = false;

  const splitText = new SplitText(text, {
    type: 'chars',
  });

  const splitAbsoluteText = new SplitText(absoluteText, {
    type: 'chars',
  });

  const openTl = gsap.timeline({ paused: true });

  gsap.set(navMobile, { yPercent: 0 });
  gsap.set(navLinks, { opacity: 0, x: '-2rem' });
  gsap.set(btnWrap, { opacity: 0, x: '2rem' });

  openTl.to(navMobile, {
    yPercent: 100,
    duration: 0.8,
    ease: 'custom',
  });
  openTl.to(
    splitText.chars,
    {
      yPercent: -100,
      duration: 0.5,
      ease: 'custom',
      stagger: {
        amount: 0.1,
        from: 'start',
      },
    },
    0
  );
  openTl.to(
    splitAbsoluteText.chars,
    {
      yPercent: -100,
      duration: 0.6,
      ease: 'custom',
      stagger: {
        amount: 0.1,
        from: 'start',
      },
    },
    0
  );
  openTl.to(
    navLinks,
    {
      opacity: 1,
      x: '0rem',
      duration: 0.8,
      ease: 'custom',
      stagger: {
        amount: 0.3,
        from: 'start',
      },
    },
    0.3
  );
  openTl.to(
    btnWrap,
    {
      opacity: 1,
      x: '0rem',
      duration: 0.8,
      ease: 'custom',
    },
    0.4
  );

  toggle.addEventListener('click', () => {
    if (!isOpen) {
      openTl.play();
      isOpen = true;
    } else {
      openTl.reverse();
      isOpen = false;
    }
  });

  navLinks.forEach((link) => {
    link.addEventListener('click', () => {
      if (isOpen) {
        openTl.reverse();
        isOpen = false;
      }
    });
  });

  if (btnWrap) {
    btnWrap.addEventListener('click', () => {
      if (isOpen) {
        openTl.reverse();
        isOpen = false;
      }
    });
  }
}
